import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { CommonService } from './common.service';
import { environment } from '@env/environment';
import { TranslateService } from '@ngx-translate/core';
import { PageResponse } from '@app/core/models/query/page-response';
import { CustomerInformationMaster } from '@app/core/models/customer-information/customer-information-master';
import { CardRequest } from '@app/core/models/scan-card/card-request';
import { CardDto } from '@app/core/models/scan-card/card-dto';

@Injectable({
    providedIn: 'root'
})
export class CustomerInformationService {

    constructor(
        private httpClient: HttpClient,
        private commonService: CommonService,
        private translate: TranslateService
    ) { }



    get(secretKey: string): Observable<CustomerInformationMaster> {
        let params: HttpParams = new HttpParams();
        params = params.set('secretKey', secretKey);
        return this.httpClient.get(`${environment.API_END_POINT}/customer_informations`,
            {
                headers: new HttpHeaders({
                    'Content-Type': 'application/json',
                }),
                params: params
            }
        ).pipe(
            catchError((error: Response | any) => this.commonService.handleHttpError(error))
        );
    }

    scanOcr(request: CardRequest): Observable<CardDto> {

        // let body = JSON.stringify(request, this.commonService.internalPropsReplacer);
    
        return this.httpClient.post(`${environment.API_END_POINT}/id-card-ocr-request`, request,
          {
            headers: new HttpHeaders({
              'Content-Type': 'application/json',
            }),
          }
        ).pipe(
          map(response => (<CardDto>response)),
          catchError((error: Response | any) => this.commonService.handleHttpError(error))
        );
      }

}
