import { Component, ElementRef, OnInit, Renderer2, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, Validators } from '@angular/forms';
import { validatorsByReg, svgPathToGeoJson, idcardValidators } from '../../utils/judge-data'
import SignaturePad from 'signature_pad'
import html2canvas from 'html2canvas';
import { MemberService } from '@app/core/services/api-service/member.service';
import { LocaleFilterPipe } from '@app/core/pipes/locale-filter.pipe';
import { I18nString } from '@app/core/models/i18n/i18n-string';
import { TranslateService } from '@ngx-translate/core';
import { catchError, finalize, map, skip, switchMap, tap } from 'rxjs/operators';
import { BehaviorSubject, merge, Observable, of } from 'rxjs';
import { SessionService } from '@app/core/services/front-end-service/session.service';
import { LayoutService } from '@app/core/services/front-end-service/layout.service';
import { Locale } from '@app/core/types/locale';
import { ActivatedRoute } from '@angular/router';
import { CustomerInformationService } from '@app/core/services/api-service/customer_information.service';
import { CardRequest } from '@app/core/models/scan-card/card-request';
import { ErrorResponse } from '@app/core/models/error/error.response';
import { ToastrService } from 'ngx-toastr';
import { PROVINCE_LIST, CITY_LISTt, COUNTY_LIST } from '@app/core/utils/area'
import { CardDto } from '@app/core/models/scan-card/card-dto';
import { AreaDto, AreaOcr } from '@app/core/models/area/area-dto';


interface Location {
  id: string,
  countryCode: string,
  name: Array<I18nObj>,
  sequence: number
}

interface I18nObj {
  locale: string,
  value: string
}

enum CountryType {
  HONG_KONG = 'HONG_KONG',
  CHINA = 'CHINA',
  MACAU = '澳門:MACAU',
  OTHERS = '其他:OTHERS',
}

@Component({
  selector: 'app-member-info-register',
  templateUrl: './member-info-register.component.html',
  styleUrls: ['./member-info-register.component.scss']
})
export class MemberInfoRegisterComponent implements OnInit {

  @ViewChild('signaturePad', { static: false }) signaturePad: ElementRef<HTMLCanvasElement>
  @ViewChild('myBox') myBox: ElementRef;
  @ViewChild('video') video: ElementRef;
  @ViewChild('canvas') canvas: ElementRef;
  @ViewChild('ocr') ocr: ElementRef;

  managerId = {
    tempUmhBookingId: undefined,
    shopId: undefined,
    memberId: undefined,
    countiesId: undefined,
    districtsId: undefined,
    regionsId: undefined,
  }
  screenshotPhoto: string
  managerStatus = {
    submitted: false,
    complete: false,
    isScNameShow: true,
    isTcNameShow: true,
    isEnNameShow: true,
    isGenderShow: true,
    isIdCardShow: true,
    isBornDateShow: true,
    isPhoneNumberShow: true,
    isEmailShow: true,
    isAddressShow: true,
    isDetailsAddressShow: true,
    countryCode: undefined,
    regionCode: undefined,
    districtCode: undefined,
    regionCodeSet: new Set(),
    idCardCategoryByValid: 'HKID',
    idCardCategoryByVal: CountryType.HONG_KONG,
    formalMember: false,
    phoneAreaCodeByValid: /^([69]\d{7})$/,
    scrollY: 0,
    matOptions: 'specialOptions',
  }

  managerValue = {
    shopAddress: undefined,
    bookingName: undefined,
    bookingTime: undefined,
    bookingTimeEn: undefined,
    bookingAddress: undefined,
    address1: undefined,
    country: undefined,
    district: undefined,
    region: undefined,
  }

  managerObj = {
    canvas: undefined,
    signaturePad: undefined,
    signature: undefined,
    counties: undefined,
    districts: undefined,
    regions: undefined,
    idcardCategory: [
      {
        key: 'HKID',
        text: "PRE_REGISTER.HK_IDCARD",
        val: CountryType.HONG_KONG
      },
      {
        key: 'MOID',
        text: "PRE_REGISTER.MO_IDCARD",
        val: CountryType.MACAU
      },
      {
        key: 'CHID',
        text: "PRE_REGISTER.CH_IDCARD",
        val: CountryType.CHINA
      },
      {
        key: 'OTHID',
        text: "PRE_REGISTER.OTH_IDCARD",
        val: CountryType.OTHERS
      }
    ],

    phoneAreaCodeArray: ['852', '853', '86']

  }


  areaGroup1Options: Observable<Array<any>>
  areaGroup2Options: Observable<Array<any>>
  areaGroup3Options: Observable<Array<any>>


  form = this.formBulider.group({
    chFirstName: ['', validatorsByReg("chFirstName", /^[\u4e00-\u9fa5]*$/)],
    chLastName: ['', validatorsByReg("chLastName", /^[\u4e00-\u9fa5]*$/)],
    enFirstName: ['', validatorsByReg("enFirstName", /^[a-zA-Z ]+$/)],
    enLastName: ['', validatorsByReg("enLastName", /^[a-zA-Z ]+$/)],
    sex: ['M'],
    idcardCategory: [''],
    idcardNumber: ['', idcardValidators(this.managerStatus.idCardCategoryByValid)],
    bornYear: ['', Validators.required],
    bornMonth: ['', Validators.required],
    bornDay: ['', Validators.required],
    phoneAreaCode: [''],
    phoneNumber: ['', validatorsByReg("phone", this.managerStatus.phoneAreaCodeByValid)],
    email: ['', validatorsByReg("email", /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/)],
    areaGroup1: [''],
    areaGroup2: [''],
    areaGroup3: [''],
    detailAddress: [''],
    consent1: [''],
    consent2: [''],
    consent3: [''],
  })

  isShowOcr: boolean = false;

  idCardIndex: number = 0;

  isShowVideo: boolean = false;

  isShowCanvas: boolean = true;

  imageBase64: string = '';

  // areaArray1: AreaDto[] = [];

  // areaByOcr: object = {
  //   areaValue1: '',
  //   areaValue2: '',
  //   areaValue3: '',
  // }

  // ocrArea: AreaOcr = {
  //   province: '',
  //   city: '',
  // }





  get f(): { [Key: string]: AbstractControl } {
    return this.form.controls
  }

  constructor(
    private formBulider: FormBuilder,
    private memberService: MemberService,
    public translateService: TranslateService,
    public sessionService: SessionService,
    private layoutService: LayoutService,
    private route: ActivatedRoute,
    private renderer: Renderer2,
    private customerInformationService: CustomerInformationService,
    private toastrService: ToastrService,
  ) {
    this.managerStatus.submitted = false
    this.managerObj.canvas = null
  }


  ngOnInit() {
    window.addEventListener('scroll', () => {
      this.managerStatus.scrollY = window.scrollY
      if (this.managerStatus.scrollY < 100) {
        this.managerStatus.matOptions = "specialOptions"
      } else {
        this.managerStatus.matOptions = "matOptions"
      }
    })

    //禁用ios 10 以上的手机页面缩放功能
    document.addEventListener('gesturestart', function (event) {
      event.preventDefault();
    });
    document.documentElement.addEventListener('touchstart', function (event) {
      if (event.touches.length > 1) {
        event.preventDefault();
      }
    }, false);

    this.layoutService.showLoading();

    this.route.params.subscribe(params => {
      this.managerId.tempUmhBookingId = params['bookingId'];
      this.memberService.getBookingMemberInfo(this.managerId.tempUmhBookingId).pipe(
        finalize(() => {
          this.layoutService.closeLoading();
        })
      ).subscribe((data) => {
        this.layoutService.closeLoading();
        this.managerStatus.formalMember = !data.formalMember
        this.managerId.memberId = data.memberId
        this.managerValue.bookingName = data.memberName
        this.managerValue.shopAddress = data.shopAddress

        this.managerValue.bookingTime = data.bookingTime
        this.managerValue.bookingTimeEn = data.bookingTime.replace(/年|月/g, '-')
        this.managerValue.bookingTimeEn = this.managerValue.bookingTimeEn.replace(/日/g, '');
        this.managerValue.bookingAddress = data.bookingAddress
        this.managerId.shopId = data.shopId

        // 逻辑修改为只需要带出默认的香港id，所以这三个变量失去作用
        // this.managerId.countiesId = data.countiesId
        // this.managerId.districtsId = data.districtsId
        // this.managerId.regionsId = data.regionsId

        this.managerValue.address1 = data.address1
        this.managerStatus.isScNameShow = (!this.managerStatus.formalMember || this.isShowByAllDirty(data.scFirstName, data.scLastName));
        this.managerStatus.isTcNameShow = (!this.managerStatus.formalMember || this.isShowByAllDirty(data.tcFirstName, data.tcLastName));
        this.managerStatus.isEnNameShow = (!this.managerStatus.formalMember || this.isShowByAllDirty(data.enFirstName, data.enLastName));
        this.managerStatus.isGenderShow = (!this.managerStatus.formalMember || data.gender)
        this.managerStatus.isIdCardShow = (!this.managerStatus.formalMember || data.hkid)
        this.managerStatus.isBornDateShow = (!this.managerStatus.formalMember || data.dob)
        this.managerStatus.isPhoneNumberShow = (!this.managerStatus.formalMember || data.mobileNo)
        this.managerStatus.isEmailShow = (!this.managerStatus.formalMember || data.email)
        this.managerStatus.isAddressShow = (!this.managerStatus.formalMember || data.showAreaSelection || data.address1)
        this.managerStatus.isDetailsAddressShow = (!this.managerStatus.formalMember || !data.address1)
        //this.f.consent3.setValue(data.optOut);

        //this.setAreaNameById(this.managerId.countiesId,this.managerId.regionsId,this.managerId.districtsId);

        this.memberService.getCounties().subscribe((data) => {
          // if(!this.managerId.countiesId){
          //   this.managerId.countiesId = data.find((item)=> item.countryCode == '1').id;
          // }
          // if (this.areaArray1.length == 0) {
          //   this.areaArray1 = JSON.parse(JSON.stringify(data))
          // }
          this.managerId.countiesId = data.find((item) => item.countryCode == '1').id;
          this.f.areaGroup1.setValue(this.getNameByCurrentLang(data.find((item) => item.countryCode == '1').name));
          this.managerObj.counties = data
          this.memberService.getRegions().subscribe((data) => {
            let regionsCodeSet = new Set();
            this.managerObj.regions = data.filter((item) => {
              if (item.countryCode.toString() === '1') {
                regionsCodeSet.add(item.regionCode)
                return item
              }
            })
            // this.memberService.getDistricts().subscribe((data)=>{
            //   this.managerObj.districts =  data.filter((item)=> regionsCodeSet.has(item.regionCode))
            // })
          })
          this.areaGroup2Options = this.f.areaGroup1.valueChanges.pipe(
            switchMap(() => this.memberService.getRegions()),
            map((arr) => arr.filter((item) => item.countryCode?.toString() === this.managerStatus.countryCode?.toString())),
            tap((val) => {
              this.managerId.districtsId = undefined;
              this.managerId.regionsId = undefined;
              this.f.areaGroup2.setValue(null);
              this.f.areaGroup3.setValue(null);
              this.managerStatus.regionCode = undefined;
              // if (this.ocrArea.province != '') {
              //   let areaValue2 = val.find((item: AreaDto) => item.name.find(res => this.ocrArea.province.includes(res.value)));
              //   this.setAreaByOcr(areaValue2, 'regionsId', 'areaGroup2');
              //   this.ocrArea.province = '';
              // }
            })
          )

          this.areaGroup3Options = this.f.areaGroup2.valueChanges.pipe(

            switchMap(() => this.memberService.getDistricts()),
            map((arr) => arr.filter((item) => item.regionCode?.toString() === this.managerStatus.regionCode?.toString())),
            tap((val) => {
              this.managerId.districtsId = undefined;
              this.f.areaGroup3.setValue(null);
              // if (this.ocrArea.city != '') {
              //   let areaValue3 = val.find((item: AreaDto) => item.name.find(res => this.ocrArea.city.includes(res.value)));
              //   this.setAreaByOcr(areaValue3, 'districtsId', 'areaGroup3');
              //   this.ocrArea.city = '';
              // }
            })
          )

        })

      })

    });

  }

  ngAfterViewInit(): void {
    this.initCanvas()
  }

  initCanvas() {
    let host = this
    window.onresize = this.resizeCanvas;
    this.resizeCanvas();
    this.managerObj.signaturePad = new SignaturePad(this.managerObj.canvas, {
      backgroundColor: '#F7F7F7'
    });


    document.getElementById('clear').addEventListener('click', function () {
      host.managerObj.signaturePad.clear();
    });

    // document.getElementById('undo').addEventListener('click', function () {
    //   let data = host.managerObj.signaturePad.toData();
    //   if (data) {
    //     data.pop(); // remove the last dot or line
    //     host.managerObj.signaturePad.fromData(data);
    //   }
    // });

  }

  saveSignature() {

    let host = this;
    // if (this.managerObj.signaturePad.isEmpty()) {
    //   return alert("Please provide a managerObj.signature first.");
    // }
    let data = this.managerObj.signaturePad.toDataURL('image/svg+xml');


    let path = atob(data.split(',')[1]);

    host.managerObj.signature = svgPathToGeoJson(path);
    //console output signature format
    //console.log(JSON.stringify(host.managerObj.signature)); 
  }

  resizeCanvas() {
    try {
      this.managerObj.canvas = this.signaturePad.nativeElement;
      let ratio = Math.max(window.devicePixelRatio || 1, 1);
      this.managerObj.canvas.width = this.managerObj.canvas.offsetWidth * ratio;
      this.managerObj.canvas.height = this.managerObj.canvas.offsetHeight * ratio;
      this.managerObj.canvas.getContext("2d").scale(ratio, ratio);
    } catch (error) {

    }


  }

  formInvalidCheck() {
    if (!this.f.chFirstName.valid || !this.f.chLastName.valid || (!this.f.enFirstName.valid && this.managerStatus.isEnNameShow) || (!this.f.enLastName.valid && this.managerStatus.isEnNameShow) ||
      (!this.f.idcardNumber.valid && this.managerStatus.isIdCardShow) || (!this.f.phoneNumber.valid && this.managerStatus.isPhoneNumberShow) || (!this.f.email.valid && this.managerStatus.isEmailShow) ||
      (!this.f.bornDay.valid && this.managerStatus.isBornDateShow) || (!this.f.bornMonth.valid && this.managerStatus.isBornDateShow) || (!this.f.bornYear.valid && this.managerStatus.isBornDateShow) ||
      !this.f.consent1.value || !this.f.consent2.value) {
      return false;
    }

    return true;
  }


  setAreaNameById(id1, id2, id3) {

    this.memberService.getCounties().subscribe(data => this.managerValue.country = data.find((item) => {
      if (item.id == id1) {
        this.f.areaGroup1.setValue(this.getNameByCurrentLang(item.name));
        this.managerStatus.countryCode = item.countryCode;
        this.managerId.countiesId = id1;
        this.memberService.getRegions().subscribe(data => this.managerValue.region = data.find((item) => {
          if (item.id == id2) {
            this.f.areaGroup2.setValue(this.getNameByCurrentLang(item.name));
            this.managerStatus.regionCode = item.regionCode;
            this.managerId.regionsId = id2;
            this.memberService.getDistricts().subscribe(data => this.managerValue.district = data.find((item) => {
              if (item.id == id3) {
                this.f.areaGroup3.setValue(this.getNameByCurrentLang(item.name));
                this.managerStatus.districtCode = item.districtCode;
                this.managerId.districtsId = id3;
              }
            }))
          }
        }))
      }


    }))


  }

  async submitForm() {
    this.managerStatus.submitted = true;

    //如果通过检验就调用传输数据的接口
    if (this.formInvalidCheck()) {
      await this.screenshot()
      this.layoutService.showLoading();
      this.saveSignature()
      let formData = {
        "umhBookingId": this.managerId.tempUmhBookingId,
        "memberId": this.managerId.memberId,
        "chineseFirstName": this.f.chFirstName.value,
        "chineseLastName": this.f.chLastName.value,
        "englishFirstName": this.f.enFirstName.value,
        "englishLastName": this.f.enLastName.value,
        "gender": this.f.sex.value,
        "country": this.managerStatus.idCardCategoryByVal,
        "hkid": this.f.idcardNumber.value,
        "dob": `${this.f.bornYear.value}-${this.f.bornMonth.value?.toString().padStart(2, '0')}-${this.f.bornDay.value?.toString().padStart(2, '0')}`,
        "tel3": `${this.f.phoneAreaCode.value}${this.f.phoneNumber.value}`,
        "email": this.f.email.value,
        "shopId": this.managerId.shopId,
        "countiesId": this.managerId.countiesId,
        "districtsId": this.managerId.districtsId,
        "regionsId": this.managerId.regionsId,
        "signature": JSON.stringify(this.managerObj.signature),
        "address1": this.f.detailAddress.value,
        "formalUsername": true,
        "optOut": !!(this.f.consent3.value),
        "screenshot": this.screenshotPhoto
      }

      if (!this.managerStatus.isScNameShow && this.managerStatus.formalMember) {
        delete formData.chineseFirstName
        delete formData.chineseLastName
      }

      if (!this.managerStatus.isEnNameShow && this.managerStatus.formalMember) {
        delete formData.englishFirstName
        delete formData.englishLastName
      }

      if (!this.managerStatus.isGenderShow && this.managerStatus.formalMember) {
        delete formData.gender
      }


      if (!this.managerStatus.isIdCardShow && this.managerStatus.formalMember) {
        delete formData.hkid
      }

      if (!this.managerStatus.isBornDateShow && this.managerStatus.formalMember) {
        delete formData.dob
      }

      if (!this.managerStatus.isPhoneNumberShow && this.managerStatus.formalMember) {
        delete formData.tel3
      }

      if (!this.managerStatus.isAddressShow && this.managerStatus.formalMember) {
        delete formData.countiesId
        delete formData.districtsId
        delete formData.regionsId
        delete formData.address1
      }

      if (!this.managerStatus.isEmailShow && this.managerStatus.formalMember) {
        delete formData.email
      }

      for (let perproty in formData) {
        if (formData[perproty] === "" || formData[perproty] === undefined || formData[perproty] === null) {
          delete formData[perproty]
        }
      }

      this.memberService.putMemberInfo(this.managerId.memberId, formData).pipe(
        finalize(() => {
          this.layoutService.closeLoading();
        })
      ).subscribe((data) => {
        this.layoutService.closeLoading();
        this.managerStatus.complete = true;
      })
    }

  }


  isShowByAllDirty(...args) {
    for (let i = 0; i < args.length; i++) {
      if (args[i]) {
        return true
      }
    }

    return false
  }

  getNameByCurrentLang(item) {
    return new LocaleFilterPipe<I18nString>().transform(
      item,
      this.translateService.currentLang
    )?.value;
  }

  getNameByI18String(str) {
    return this.translateService.instant(str)
  }

  selectOption(item, name) {
    this.managerId[`${name}`] = item.id;
    //console.log("name",this.managerId[`${name}`])
    switch (name) {
      case 'countiesId':
        this.managerStatus.countryCode = item.countryCode;
        break;
      case 'regionsId':
        this.managerStatus.regionCode = item.regionCode;
        break;
      case 'districtsId':
        this.managerStatus.districtCode = item.districtCode;
        break;
    }

  }

  setAreaByOcr(item, name, area) {
    this.selectOption(item, name);
    this.f[area].setValue(this.getNameByCurrentLang(item.name))
  }


  selectIdCardCategory(item) {
    this.managerStatus.idCardCategoryByVal = item.val;
    this.managerStatus.idCardCategoryByValid = item.key
    this.f.idcardNumber.clearValidators();
    this.f.idcardNumber.setValidators(idcardValidators(this.managerStatus.idCardCategoryByValid))
    this.f.idcardNumber.updateValueAndValidity();
  }

  setIdCardValueByOcr(item) {
    this.f.idcardCategory.setValue(item.text)
    this.selectIdCardCategory(item);
  }

  getAllBeforeYear() {
    let currentYear = new Date().getFullYear()
    let resArr = []
    for (let i = currentYear; i > 1900; i--) {
      resArr.push(i)
    }
    return resArr
  }


  getAllMonth() {
    let resArr = []
    for (let i = 1; i < 13; i++) {
      resArr.push(i)
    }
    return resArr
  }

  getAllDay() {
    let resArr = []
    for (let i = 1; i < 32; i++) {
      resArr.push(i)
    }
    return resArr
  }

  changeLang(lang: Locale) {
    this.layoutService.showLoading();
    setTimeout(() => {
      this.sessionService.currentLang$.next(lang);
      this.layoutService.closeLoading();
    }, 200);
  }


  checkAllFieldComplete() {
    if (
      (
        this.managerStatus.isScNameShow &&
        this.managerStatus.isTcNameShow
      ) ||
      this.managerStatus.isEnNameShow ||
      this.managerStatus.isGenderShow ||
      this.managerStatus.isIdCardShow ||
      this.managerStatus.isBornDateShow ||
      this.managerStatus.isPhoneNumberShow ||
      this.managerStatus.isEmailShow ||
      this.managerStatus.isAddressShow) {

      return false
    } else {
      return true
    }
  }

  checkAllPersonFieldComplete() {
    if (
      (
        this.managerStatus.isScNameShow &&
        this.managerStatus.isTcNameShow
      ) ||
      this.managerStatus.isEnNameShow ||
      this.managerStatus.isGenderShow ||
      this.managerStatus.isIdCardShow ||
      this.managerStatus.isBornDateShow) {
      return false
    } else {
      return true
    }
  }

  selectPhoneAreaCode(code) {
    if (code == '852') {
      this.managerStatus.phoneAreaCodeByValid = /^([69]\d{7})$/
    } else if (code == '853') {
      this.managerStatus.phoneAreaCodeByValid = /^([6]\d{5})$/
    } else {
      this.managerStatus.phoneAreaCodeByValid = /^(1[3-9]\d{9})$/
    }

    this.f.phoneNumber.clearValidators();
    this.f.phoneNumber.setValidators(validatorsByReg("phone", this.managerStatus.phoneAreaCodeByValid))
    this.f.phoneNumber.updateValueAndValidity();

  }

  // 截图
  async screenshot() {
    // const canvasContent = document.getElementById('wrapper-memberinfo-register'),//需要截图的包裹的（原生的）DOM 对象
    // width = canvasContent.clientWidth,//canvasContent.offsetWidth || document.body.clientWidth; //获取dom 宽度
    // height = canvasContent.clientHeight,//canvasContent.offsetHeight; //获取dom 高度
    // canvas = document.createElement("canvas"), //创建一个canvas节点
    // scale = 4; //定义任意放大倍数 支持小数
    // canvas.width = width * scale; //定义canvas 宽度 * 缩放
    // canvas.height = height * scale; //定义canvas高度 *缩放
    // canvas.style.width = canvasContent.clientWidth * scale + "px";
    // canvas.style.height = canvasContent.clientHeight * scale + "px";
    // canvas.getContext("2d").scale(scale, scale); //获取context,设置scale
    // console.log(width, height)
    // const opts = {
    //     dpi: window.devicePixelRatio * 4,
    //     scale: scale, // 添加的scale 参数
    //     canvas: canvas, //自定义 canvas
    //     width: width, //dom 原始宽度
    //     height: height,
    //     scrollY: 0, 
    //     scrollX: 0,
    //     x: 0, // x坐标
    //     y: 0, // y坐标
    //     useCORS: true // 【重要】开启跨域配置
    // };
    document.querySelector('.logobar').className += ' position-static'
    document.querySelector('.textarea-title').className += ' padding-0'
    await html2canvas(this.myBox.nativeElement, {
      scale: 1.5,
      useCORS: true
    }).then(canvas => {
      document.querySelector('.logobar').classList.remove('position-static')
      document.querySelector('.textarea-title').classList.remove('padding-0')
      this.screenshotPhoto = canvas.toDataURL()
    });
  }

  // 打开OCR
  openOcr() {
    this.isShowOcr = true;
    this.renderer.removeClass(document.body, 'overflow-auto');
    this.renderer.addClass(document.body, 'overflow-hidden');
    this.openCapture()
  }

  // 关闭OCR
  closeOcr() {
    this.isShowOcr = false;
    this.isShowCanvas = true;
    this.isShowVideo = false;
    this.video.nativeElement.srcObject.getTracks()[0].stop();
    this.renderer.removeClass(document.body, 'overflow-hidden');
    this.renderer.addClass(document.body, 'overflow-auto');
  }

  selectIdCard(num: number) {
    if (this.idCardIndex == num) return;
    this.idCardIndex = num;
  }

  getPhone() {
    this.canvas.nativeElement.width = this.video.nativeElement.clientWidth;
    this.canvas.nativeElement.height = this.video.nativeElement.clientHeight;
    this.isShowVideo = true;
    this.isShowCanvas = false;
    this.video.nativeElement.pause();

    let ctx = this.canvas.nativeElement.getContext("2d");
    ctx.drawImage(this.video.nativeElement, 0, 0, this.canvas.nativeElement.width, this.canvas.nativeElement.height);
    let imgData = this.canvas.nativeElement.toDataURL();
    this.imageBase64 = imgData;
    this.stopCapture()

  }

  setPhone() {
    this.isShowCanvas = true;
    this.isShowVideo = false;
    this.openCapture();
  }

  // // 打开摄像机
  openCapture() {
    this.imageBase64 = '';
    this.video.nativeElement.setAttribute('autoplay', '');
    this.video.nativeElement.setAttribute('muted', '');
    this.video.nativeElement.setAttribute('playsinline', '');

    // 旧版本浏览器可能根本不支持mediaDevices，我们首先设置一个空对象
    if (navigator.mediaDevices === undefined) {
      Object.defineProperty(navigator, 'mediaDevices', {
        value: {},
        writable: false
      })
    }

    // 一些浏览器实现了部分mediaDevices，我们不能只分配一个对象
    // 使用getUserMedia，因为它会覆盖现有的属性。
    // 这里，如果缺少getUserMedia属性，就添加它。

    if (navigator.mediaDevices.getUserMedia === undefined) {
      Object.defineProperty(navigator.mediaDevices, 'getUserMedia', {
        value: function (constraints) {
          var getUserMedia =
            navigator['webkitGetUserMedia'] ||
            navigator['mozGetUserMedia'] ||
            navigator['getUserMedia'];
          if (!getUserMedia) {
            return Promise.reject(
              new Error("getUserMedia is not implemented in this browser")
            );
          }
          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        },
        writable: false
      })
    }

    let constraints = {
      audio: false,
      video: {
        facingMode: { exact: "environment" },
      },
    };

    navigator.mediaDevices
      .getUserMedia(constraints)
      .then((stream) => {
        // 旧的浏览器可能没有srcObject
        if ("srcObject" in this.video.nativeElement) {
          this.video.nativeElement.srcObject = stream;
        } else {
          // 避免在新的浏览器中使用它，因为它正在被弃用。
          this.video.nativeElement.src = window.URL.createObjectURL(stream);
        }
        this.video.nativeElement.onloadedmetadata = (e) => {
          this.video.nativeElement.play();
        };
      })
      .catch((err) => {
        console.log(err);
      });

  }




  // 停止摄像机
  stopCapture() {
    var video = this.video.nativeElement;
    if (!video.srcObject) return;
    let stream = video.srcObject;
    let tracks = stream.getTracks();
    tracks.forEach(track => {
      track.stop()
    })
  }

  // 提交OCR
  submmitOcr() {
    if (this.imageBase64 == '') {
      this.getPhone();
    };
    this.layoutService.showLoading();
    let cardRequest = new CardRequest();
    cardRequest.imageBase64 = this.imageBase64;
    cardRequest.idCardType = this.idCardIndex == 0 ? 'HK' : 'CN';
    let scanOcr = this.customerInformationService.scanOcr(cardRequest).pipe(
      catchError(
        (error: ErrorResponse) => {
          scanOcr.unsubscribe();
          this.toastrService.error(
            this.translateService.instant(`OCR.${error.errors[0].errorCode}`)
          );
          return of(null);
        }
      ),
      finalize(() => {
        this.closeOcr();
        this.layoutService.closeLoading();
      }),
    ).subscribe((res: CardDto) => {
      this.f.chLastName.setValue(res.cnName.slice(0, 1))
      this.f.chFirstName.setValue(res.cnName.substr(1))
      if (res.objectType == 'HKIDCard') {
        this.setDataHkByOcr(res)
      } else {
        this.setDataCnByOcr(res)
      }
      this.f.idcardNumber.setValue(res.idNum)
      this.f.sex.setValue(res.sex)
    })
  }

  // OCR处理香港身份证信息
  setDataHkByOcr(data: CardDto) {
    if (data.enName.indexOf(',') > -1) {
      this.f.enLastName.setValue(data.enName.split(',')[0]);
      this.f.enFirstName.setValue(data.enName.split(',')[1]);
    } else if (data.enName.indexOf('.') > -1) {
      this.f.enLastName.setValue(data.enName.split('.')[0]);
      this.f.enFirstName.setValue(data.enName.split('.')[1]);
    } else {
      this.f.enLastName.setValue(data.enName.split(' ')[0]);
      this.f.enFirstName.setValue(data.enName.split(' ')[1]);
    }
    this.setIdCardValueByOcr({
      key: 'HKID',
      text: "PRE_REGISTER.HK_IDCARD",
      val: CountryType.HONG_KONG
    })
    this.f.bornYear.setValue(+data.birthday.split('-')[0]);
    this.f.bornMonth.setValue(+data.birthday.split('-')[1]);
    this.f.bornDay.setValue(+data.birthday.split('-')[2]);
  }

  // OCR处理中国内地身份证信息
  setDataCnByOcr(data: CardDto) {
    this.f.enLastName.setValue('');
    this.f.enFirstName.setValue('');
    this.setIdCardValueByOcr({
      key: 'CHID',
      text: "PRE_REGISTER.CH_IDCARD",
      val: CountryType.CHINA
    })
    this.f.bornYear.setValue(+data.birthday.split('/')[0]);
    this.f.bornMonth.setValue(+data.birthday.split('/')[1]);
    this.f.bornDay.setValue(+data.birthday.split('/')[2]);

    // this.ocrArea.province = Object.values(this.manageAreaData(PROVINCE_LIST).find(item => Object.keys(item)[0].substr(0, 2) == data.idNum.substr(0, 2)))[0] as string;
    // this.ocrArea.city = Object.values(this.manageAreaData(CITY_LISTt).find(item => Object.keys(item)[0].substr(0, 4) == data.idNum.substr(0, 4)))[0] as string;
    // let areaValue1 = this.areaArray1.find((item: AreaDto) => item.name.find((val) => val.value == '中国'));
    // this.setAreaByOcr(areaValue1, 'countiesId', 'areaGroup1');
    // if (data.address.indexOf(this.ocrArea.city) > -1) {
    //   this.f.detailAddress.setValue(data.address.split(`${this.ocrArea.city}`)[1]);
    // } else if (data.address.indexOf(this.ocrArea.province) > -1) {
    //   this.f.detailAddress.setValue(data.address.split(`${this.ocrArea.province}`)[1]);
    // } else {
    //   this.f.detailAddress.setValue(data.address);
    // }
  }

  // 处理地址数据
  manageAreaData(data) {
    let keys = Object.keys(data);
    let values = Object.values(data);
    let result = [];
    keys.forEach((item, index) => {
      let obj = {};
      obj[`${item}`] = values[index];
      result.push(obj);
    });
    return result;
  }


}
